
body {
  background: #eee;
  height: 100vh;
  margin: 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-flow: column;
      -ms-flex-flow: column;
          flex-flow: column;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
}

h1, h2 {
  margin: .2em 0;
  color: rgb(0, 127, 167);
}

.sled {
  width: 50em;
  height: 25em;
  border: solid rgb(0, 127, 167);
  border-width: 0 .2em;
}

.sled .dots .dotsWrapper .dot.active {
  background: rgb(0, 127, 167);
}

.sled .dots .dotsWrapper .dot {
  width: 1.5em;
  height: 1.5em;
}

.img {
  background: #fff;
  width: 40%;
  height: 80%;
}

.blue {
  background: rgb(0, 127, 167);
  width: 90%;
  height: 90%;
}



